@import "../Core/variables.scss";

.share {
    border-bottom: solid 0.2rem $lightGrey;
}

.collapsable-content {
    text-transform: uppercase;
}

.button__input {
    display: none;
}

.button__box {
    display: flex;
    justify-content: center;
}

.button {
    background: $lightGrey;
    width: 199px;
    height: 50px;
    border-radius: 12px;
    box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.32);
    display: flex;
    justify-content: center;
    align-items: center;

    color: $white;
    margin: 20px auto;
}

.js-button-active {
    background: $mediumOrange;
}

.fa-address-card {
    color: $white;
    padding-right: 0.9rem;
}

.button__title {
    font-size: 1.8rem;
    font-family: $opensans;
}

.share__twitter {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-top: solid 0.1rem $lightGrey;
    padding: 2rem;

    &-button {
        border-radius: 3px;
        box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.32);
        background-color: $azure;
        width: 18.1rem;
        height: 3rem;
        background-color: $azure;
        border-style: none;
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: $opensans;
        text-decoration: none;
    }

    &-url {
        color: $mediumOrange;
        margin-bottom: 1rem;
        font-size: 1.2rem;
    }

    &-message {
        margin-bottom: 0.5rem;
        font-size: 1.8rem;
        font-weight: 600;
    }

    &-a {
        text-decoration: none;
        color: white;
    }
}

.hiddenButton {
    display: none;
}

.fa-twitter {
    margin-right: 10px;
}